<template>
  <section>
    <el-row>
      <el-col :span="24">
        <!--表单-->
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="商品名称">
            <el-autocomplete
              class="inline-input width-140" v-model="formInline.name" :fetch-suggestions="querySearchName"
              placeholder="请输入商品名称" :trigger-on-focus="false">
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="用友编码">
            <el-autocomplete
              class="inline-input width-140" v-model="formInline.code" :fetch-suggestions="querySearchCode"
              placeholder="请输入用友编码" :trigger-on-focus="false">
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="是否上架">
            <el-select v-model="formInline.putaway" placeholder="请选择" class="width-140">
              <el-option v-for="(item,index) in putaways" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品来源">
            <el-select v-model="formInline.source" placeholder="请选择" class="width-140">
              <el-option v-for="(item,index) in sources" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品类目">
            <el-autocomplete
              class="inline-input width-140" v-model="formInline.option" :fetch-suggestions="querySearchOption"
              placeholder="请输入商品类目" :trigger-on-focus="true">
            </el-autocomplete>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
        <!--表格-->
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column prop="spu" label="商品spu"></el-table-column>
          <el-table-column prop="source" label="商品来源"></el-table-column>
          <el-table-column prop="name" label="商品名称"></el-table-column>
          <el-table-column prop="pingtai" label="商品发布平台"></el-table-column>
          <el-table-column label="规格信息">
            <template slot-scope="scope">
              <el-popover trigger="hover" placement="top" v-for="ggitem in scope.row.guige">
                <div class="guige-pop">
                  <p>颜色: {{ ggitem.color }}</p>
                  <p>库存: {{ ggitem.kucun }}</p>
                  <p>尺寸: {{ ggitem.chicun }}</p>
                  <p>价格: 11</p>
                </div>
                <div slot="reference" class="name-wrapper">
                  <div class="td-guige">
                    颜色: {{ ggitem.color }}<br />
                    库存: {{ ggitem.kucun }}<br />
                    尺寸: {{ ggitem.chicun }}<br />
                    价格: 11
                  </div>
                </div>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column  prop="type" label="商品app类目"></el-table-column>
          <el-table-column  prop="putaway" label="是否上架"></el-table-column>
          <el-table-column  prop="addtime" label="添加时间"></el-table-column>
          <el-table-column  prop="uptime" label="更新时间"></el-table-column>
          <el-table-column  prop="upuser" label="更新人"></el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <div class="table-buttonbox">
                <el-button size="small" @click="commonEdit(scope.$index, scope.row)">编辑公共信息</el-button>
              </div>
              <div class="table-buttonbox">
                <el-button size="small" @click="typeEdit(scope.$index, scope.row)">编辑规格信息</el-button>
              </div>
              <div class="table-buttonbox">
                <el-button size="small" @click="soldOut(scope.$index, scope.row)">下架</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
            :page-size="pageSize" layout="prev, pager, next, jumper" :total="totalNum">
          </el-pagination>
        </div>
      </el-col>
    </el-row>
  </section>
</template>
<script>
  import { commodityList , commoditySearchName , commoditySearchCode , commodityTypeList , commoditySearch } from '../../api/api';
  export default {
    data () {
      return {
        formInline: {
          name: '',
          code: '',
          putaway:'all',
          source:'1',
          category:[]
        },
        putaways:[{label:'全部',value:'all'},{label:'是',value:'1'},{label:'否',value:'0'}],
        sources:[{label:'京东',value:'1'},{label:'网易',value:'2'}],
        tableData: [
          {
            spu:'123456',source:'京东',name:'鼠标',pingtai:'妙商城',
            guige:[{color:'黄色',kucun:'20',chicun:'xxl'},{color:'红色',kucun:'90',chicun:'xxl'}],
            type:'女装',putaway:'是',addtime:'2018-02-02 12:12:00',uptime:'2018-02-02 12:12:00',upuser:'miaojiankang'
          }
        ],
        options: [],
        currentPage: 1,
        pageSize: 10,
        totalNum: 86,
        table_index: 999,
        restaurantsName:[
          { "value": "三全鲜食（北新泾店）", "address": "长宁区新渔路144号" },
          { "value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号" },
          { "value": "新旺角茶餐厅", "address": "上海市普陀区真北路988号创邑金沙谷6号楼113" },
          { "value": "泷千家(天山西路店)", "address": "天山西路438号" },
          { "value": "胖仙女纸杯蛋糕（上海凌空店）", "address": "上海市长宁区金钟路968号1幢18号楼一层商铺18-101" },
          { "value": "贡茶", "address": "上海市长宁区金钟路633号" },
        ],//模拟数据
        restaurantsCode:[
          { "value": "三全鲜食（北新泾店）", "address": "长宁区新渔路144号" },
          { "value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号" },
          { "value": "新旺角茶餐厅", "address": "上海市普陀区真北路988号创邑金沙谷6号楼113" },
          { "value": "泷千家(天山西路店)", "address": "天山西路438号" },
          { "value": "胖仙女纸杯蛋糕（上海凌空店）", "address": "上海市长宁区金钟路968号1幢18号楼一层商铺18-101" },
          { "value": "贡茶", "address": "上海市长宁区金钟路633号" },
        ],//模拟数据
        restaurantsOption:[
          { "value": "三全鲜食（北新泾店）", "address": "长宁区新渔路144号" },
          { "value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号" },
          { "value": "新旺角茶餐厅", "address": "上海市普陀区真北路988号创邑金沙谷6号楼113" },
          { "value": "泷千家(天山西路店)", "address": "天山西路438号" },
          { "value": "胖仙女纸杯蛋糕（上海凌空店）", "address": "上海市长宁区金钟路968号1幢18号楼一层商铺18-101" },
          { "value": "贡茶", "address": "上海市长宁区金钟路633号" },
        ],//模拟数据
      };
    },
    created () {
      
    },
    methods: {
      onSubmit () {
        
      },
      commonEdit() {
        this.$router.push({path:'/commodity/coedit'});
      },
      typeEdit() {
        this.$router.push({path:'/commodity/tyedit'});
      },
      soldOut(index,row) {
        this.$confirm('是否确认下架该商品').then(row => {
          done();
        })
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        console.log(`当前页: ${val}`);
      },
      querySearchName(queryString, cb) {
        var restaurantsName = this.restaurantsName;
        var results = queryString ? restaurantsName.filter(this.createFilter(queryString)) : restaurantsName;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      querySearchCode(queryString, cb) {
        var restaurantsCode = this.restaurantsCode;
        var results = queryString ? restaurantsCode.filter(this.createFilter(queryString)) : restaurantsCode;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      querySearchOption(queryString, cb) {
        var restaurantsOption = this.restaurantsOption;
        var results = queryString ? restaurantsOption.filter(this.createFilter(queryString)) : restaurantsOption;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },

      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
    }
  };
</script>
<style scoped lang="scss">
  .el-pagination {
    text-align: center;
    margin-top: 30px;
  }
  .width-140{
    width:140px;
  }
  .td-guige{
    width:100%;
    font-size:12px;
    line-height: 20px;
    word-break:break-all;
    display:-webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient:vertical;
    overflow:hidden;
    border-bottom: dotted 1px #d7d7d7;
    padding: 5px 0;
  }
</style>



